.table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  text-align: left;
}

.table.table-striped tbody tr:nth-of-type(odd) {
  background: #f8f9fa;
}

.table tbody tr.active, .table.table-striped tbody tr.active {
  background: #f0f1f4;
}

.table.table-hover tbody tr:hover {
  background: #f0f1f4;
}

