  border-bottom: 0.05rem solid #e7e9ed;
  padding: 0.6rem 0.4rem;
}

.table th {
  border-bottom-width: 0.1rem;
}

.btn {
  transition: all .2s ease;
  appearance: none;
  background: #fff;
  border: 0.05rem solid #2b2f46;
  border-radius: 0.1rem;
  color: #2b2f46;
  cursor: pointer;
  display: inline-block;
  font-size: 0.8rem;
  height: 1.8rem;
  line-height: 1.2rem;
  outline: none;
  padding: 0.25rem 0.4rem;
  text-align: center;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;
}

.btn:focus {
  box-shadow: 0 0 0 0.1rem rgba(43, 47, 70, 0.2);
}

.btn:focus, .btn:hover {
  background: #8087b1;
  border-color: #25293d;
  text-decoration: none;
}

.btn:active, .btn.active {
  background: #25293d;
  border-color: #1b1e2d;
  color: #fff;
  text-decoration: none;
}

.btn:active.loading::after, .btn.active.loading::after {
  border-bottom-color: #fff;
  border-left-color: #fff;
}

.btn[disabled], .btn:disabled, .btn.disabled {
  cursor: default;
  opacity: .5;
  pointer-events: none;
}

.btn.btn-primary {
  background: #2b2f46;
  border-color: #25293d;
  color: #fff;
}

.btn.btn-primary:focus, .btn.btn-primary:hover {
  background: #212436;
  border-color: #1b1e2d;
  color: #fff;
}

.btn.btn-primary:active, .btn.btn-primary.active {
  background: #1d2030;
  border-color: #181a26;
  color: #fff;
}

.btn.btn-primary.loading::after {
  border-bottom-color: #fff;
  border-left-color: #fff;
