  border-radius: 50%;
  border-right-color: transparent;
  content: "";
  height: 1em;
  width: 1em;
}

.icon-refresh::after {
  border: .2em solid currentColor;
  border-top-color: transparent;
  border-left-color: transparent;
  content: "";
  height: 0;
  left: 80%;
  top: 20%;
  width: 0;
}

.icon-search::before {
  border: 0.1rem solid currentColor;
  border-radius: 50%;
  content: "";
  height: .75em;
  left: 5%;
  top: 5%;
  transform: translate(0, 0) rotate(45deg);
  width: .75em;
}

.icon-search::after {
  background: currentColor;
  content: "";
  height: 0.1rem;
  left: 80%;
  top: 80%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: .4em;
}

.icon-edit::before {
  border: 0.1rem solid currentColor;
  content: "";
  height: .4em;
  transform: translate(-40%, -60%) rotate(-45deg);
  width: .85em;
}

.icon-edit::after {
  border: .15em solid currentColor;
  border-top-color: transparent;
  border-right-color: transparent;
  content: "";
  height: 0;
  left: 5%;
  top: 95%;
  transform: translate(0, -100%);
  width: 0;
}

.icon-delete::before {
  border: 0.1rem solid currentColor;
  border-bottom-left-radius: 0.1rem;
  border-bottom-right-radius: 0.1rem;
  border-top: 0;
  content: "";
  height: .75em;
  top: 60%;
  width: .75em;
}

.icon-delete::after {
  background: currentColor;
  box-shadow: -.25em .2em, .25em .2em;
  content: "";
  height: 0.1rem;
  top: 0.05rem;
  width: .5em;
}

.icon-share {
  border: 0.1rem solid currentColor;
  border-radius: 0.1rem;
  border-right: 0;
  border-top: 0;
}

.icon-share::before {
  border: 0.1rem solid currentColor;
  border-left: 0;
  border-top: 0;
  content: "";
  height: .4em;
  left: 100%;
  top: .25em;
  transform: translate(-125%, -50%) rotate(-45deg);
  width: .4em;
}

.icon-share::after {
  border: 0.1rem solid currentColor;
  border-bottom: 0;
  border-right: 0;
  border-radius: 75% 0;
  content: "";
  height: .5em;
  width: .6em;
}

.icon-flag::before {
  background: currentColor;
  content: "";
  height: 1em;
  left: 15%;
  width: 0.1rem;
}

.icon-flag::after {
  border: 0.1rem solid currentColor;
  border-bottom-right-radius: 0.1rem;
  border-left: 0;
  border-top-right-radius: 0.1rem;
  content: "";
  height: .65em;
  top: 35%;
  left: 60%;
  width: .8em;
}

.icon-bookmark::before {
  border: 0.1rem solid currentColor;
  border-bottom: 0;
  border-top-left-radius: 0.1rem;
  border-top-right-radius: 0.1rem;
  content: "";
  height: .9em;
  width: .8em;
}

.icon-bookmark::after {
  border: 0.1rem solid currentColor;
  border-bottom: 0;
  border-left: 0;
  border-radius: 0.1rem;
  content: "";
  height: .5em;
  transform: translate(-50%, 35%) rotate(-45deg) skew(15deg, 15deg);
  width: .5em;
}

.icon-download,
.icon-upload {
  border-bottom: 0.1rem solid currentColor;
}

.icon-download::before,
.icon-upload::before {
  border: 0.1rem solid currentColor;
  border-bottom: 0;
  border-right: 0;
  content: "";
  height: .5em;
  width: .5em;
  transform: translate(-50%, -60%) rotate(-135deg);
}

.icon-download::after,
.icon-upload::after {
  background: currentColor;
  content: "";
  height: .6em;
  top: 40%;
  width: 0.1rem;
}

.icon-upload::before {
  transform: translate(-50%, -60%) rotate(45deg);
}

.icon-upload::after {
  top: 50%;
}

.icon-time {
  border: 0.1rem solid currentColor;
  border-radius: 50%;
}

.icon-time::before {
  background: currentColor;
  content: "";
  height: .4em;
  transform: translate(-50%, -75%);
  width: 0.1rem;
}

.icon-time::after {
  background: currentColor;
  content: "";
  height: .3em;
  transform: translate(-50%, -75%) rotate(90deg);
  transform-origin: 50% 90%;
  width: 0.1rem;
}

.icon-mail::before {
  border: 0.1rem solid currentColor;
  border-radius: 0.1rem;
  content: "";
  height: .8em;
  width: 1em;
}

.icon-mail::after {
  border: 0.1rem solid currentColor;
  border-right: 0;
  border-top: 0;
  content: "";
  height: .5em;
  transform: translate(-50%, -90%) rotate(-45deg) skew(10deg, 10deg);
  width: .5em;
}

.icon-people::before {
  border: 0.1rem solid currentColor;
  border-radius: 50%;
  content: "";
  height: .45em;
  top: 25%;
  width: .45em;
}

.icon-people::after {
  border: 0.1rem solid currentColor;
  border-radius: 50% 50% 0 0;
  content: "";
  height: .4em;
  top: 75%;
  width: .9em;
}

.icon-message {
  border: 0.1rem solid currentColor;
  border-bottom: 0;
  border-radius: 0.1rem;
  border-right: 0;
}

.icon-message::before {
  border: 0.1rem solid currentColor;
  border-bottom-right-radius: 0.1rem;
  border-left: 0;
  border-top: 0;
  content: "";
  height: .8em;
  left: 65%;
  top: 40%;
  width: .7em;
}

.icon-message::after {
  background: currentColor;
  border-radius: 0.1rem;
  content: "";
  height: .3em;
  left: 10%;
  top: 100%;
  transform: translate(0, -90%) rotate(45deg);
  width: 0.1rem;
}

.icon-photo {
  border: 0.1rem solid currentColor;
  border-radius: 0.1rem;
}

.icon-photo::before {
  border: 0.1rem solid currentColor;
  border-radius: 50%;
  content: "";
  height: .25em;
  left: 35%;
  top: 35%;
  width: .25em;
}

.icon-photo::after {
  border: 0.1rem solid currentColor;
  border-bottom: 0;
  border-left: 0;
  content: "";
  height: .5em;
  left: 60%;
  transform: translate(-50%, 25%) rotate(-45deg);
  width: .5em;
}

.icon-link::before, .icon-link::after {
  border: 0.1rem solid currentColor;
  border-radius: 5em 0 0 5em;
  border-right: 0;
  content: "";
  height: .5em;
  width: .75em;
}

.icon-link::before {
  transform: translate(-70%, -45%) rotate(-45deg);
}

.icon-link::after {
  transform: translate(-30%, -55%) rotate(135deg);
}

.icon-location::before {
  border: 0.1rem solid currentColor;
  border-radius: 50% 50% 50% 0;
  content: "";
  height: .8em;
  transform: translate(-50%, -60%) rotate(-45deg);
  width: .8em;
}

.icon-location::after {
  border: 0.1rem solid currentColor;
  border-radius: 50%;
  content: "";
  height: .2em;
  transform: translate(-50%, -80%);
  width: .2em;
}

.icon-emoji {
  border: 0.1rem solid currentColor;
  border-radius: 50%;
}

.icon-emoji::before {
  border-radius: 50%;
  box-shadow: -.17em -.15em, .17em -.15em;
  content: "";
  height: .1em;
  width: .1em;
}

.icon-emoji::after {
  border: 0.1rem solid currentColor;
  border-bottom-color: transparent;
  border-radius: 50%;
  border-right-color: transparent;
  content: "";
  height: .5em;
  transform: translate(-50%, -40%) rotate(-135deg);
  width: .5em;
